<template>
<div>
  <el-table :data="tableData">
    <el-table-column prop="_id" label="ID" width='350'>
    </el-table-column>
    <el-table-column prop="name" label="名称">
    </el-table-column>
    <el-table-column
      fixed="right"
      label="操作"
      width="200">
      <template slot-scope="scope">
        <el-button @click="$router.push(`/ads/edit/${scope.row._id}`)" type="text">编辑</el-button>
        <el-button type="text" @click="remove(scope.row)">删除</el-button>
      </template>
    </el-table-column>
  </el-table>
  </div>
</template>

<script>
export default {
  data () {
    return {
      tableData: []
    }
  },
  created () {
    this.getData()
  },
  methods: {
    async getData () {
      let res = await this.$http.get('rest/ads')
      this.tableData = res.data
    },
    remove (row) {
      this.$confirm(`将要删除 "${row.name}" , 是否继续?`, '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(async () => {
        await this.$http.delete(`rest/ads/${row._id}`)
        this.$message({
          type: 'success',
          message: '删除成功!'
        })
        this.getData()
      })
    }
  }
}
</script>

