<template>
  <div>
    <h1>{{id? "编辑":"新建"}}英雄</h1>
    <el-form label-width="80px" @submit.native.prevent="save">
      <el-tabs type='border-card' value='item'>
        <el-tab-pane label='基本信息'>
          <el-form-item label="英雄名称">
            <el-input v-model="model.name"></el-input>
          </el-form-item>
          <el-form-item label="英雄称号">
            <el-input v-model="model.title"></el-input>
          </el-form-item>
          <el-form-item label="英雄图片">
            <el-upload
              class="avatar-uploader"
              :action="$http.defaults.baseURL+'/upload'"
              :show-file-list="false"
              :on-success="handleSuccess">
              <img v-if="model.avatar" :src="model.avatar" class="avatar">
              <i v-else class="el-icon-plus avatar-uploader-icon"></i>
            </el-upload>
          </el-form-item>
          <el-form-item label="英雄分类">
            <el-select v-model="model.categories" multiple>
              <el-option v-for='item in categories' :key='item._id'
              :label='item.name' :value='item._id'
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="攻击">
            <el-rate
              style='margin-top:0.6rem;'
              v-model="model.scores.attack"
              :max='9'
              show-score>
            </el-rate>
          </el-form-item>
          <el-form-item label="难度">
            <el-rate
              style='margin-top:0.6rem;'
              v-model="model.scores.difficult"
              :max='9'
              show-score>
            </el-rate>
          </el-form-item>
          <el-form-item label="技能">
            <el-rate
              style='margin-top:0.6rem;'
              v-model="model.scores.skills"
              :max='9'
              show-score>
            </el-rate>
          </el-form-item>
          <el-form-item label="生存">
            <el-rate
              style='margin-top:0.6rem;'
              v-model="model.scores.survive"
              :max='9'
              show-score>
            </el-rate>
          </el-form-item>
          <el-form-item label="顺丰出装">
            <el-select v-model="model.item1" multiple>
              <el-option v-for='item in items' :key='item._id'
                :label='item.name' :value='item._id'
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="逆风出装">
            <el-select v-model="model.item2" multiple>
              <el-option v-for='item in items' :key='item._id'
                :label='item.name' :value='item._id'
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="使用技巧">
            <el-input type='textarea' v-model="model.usageTips"></el-input>
          </el-form-item>
          <el-form-item label="对抗技巧">
            <el-input type='textarea' v-model="model.battleTips"></el-input>
          </el-form-item>
          <el-form-item label="团战思路">
            <el-input type='textarea' v-model="model.teamTips"></el-input>
          </el-form-item>
        </el-tab-pane>
        <el-tab-pane label='技能信息' name='item'>
          <el-button @click='model.skills.push({})'><i class="el-icon-plus"></i>添加技能</el-button>
          <el-row type='flex' style='flex-flow:wrap;'>
            <el-col :md='12' v-for='(item, index) in model.skills' :key='index'>
              <el-form-item label="技能名称">
                <el-input v-model="item.name"></el-input>
              </el-form-item>
              <el-form-item label="技能图片">
                <el-upload
                  class="avatar-uploader"
                  :action="$http.defaults.baseURL+'/upload'"
                  :show-file-list="false"
                  :on-success="res => $set(item, 'icon', res.url)">
                  <img v-if="item.icon" :src="item.icon" class="avatar">
                  <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                </el-upload>
              </el-form-item>
              <el-form-item label="技能描述">
                <el-input type='textarea' v-model="item.description"></el-input>
              </el-form-item>
              <el-form-item label="对抗">
                <el-input type='textarea' v-model="item.tips"></el-input>
              </el-form-item>
              <el-button type='danger' @click='model.skills.splice(index, 1)'>删除</el-button>
            </el-col>
          </el-row>
        </el-tab-pane>
      </el-tabs>
      <el-form-item>
        <el-button type="primary" native-type="submit">立即创建</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  data () {
    return {
      categories: [],
      items: [],
      model: {
        scores: {},
        skills: []
      }
    }
  },
  created () {
    this.id && this.fetch()
    this.fetchParent()
    this.fetchCategories()
    this.fetchItems()
  },
  props: {
    id: String
  },
  methods: {
    async save () {
      // eslint-disable-next-line no-unused-vars
      let res
      if (this.id) {
        res = await this.$http.put(`rest/heroes/${this.id}`, this.model)
      } else {
        res = await this.$http.post('rest/heroes', this.model)
      }
      this.$message({
        type: 'success',
        message: '保存成功'
      })
      this.$router.push('/heroes/list')
    },
    async fetch () {
      const res = await this.$http.get(`rest/heroes/${this.id}`)
      this.model = res.data
    },
    async fetchParent () {
      const res = await this.$http.get(`rest/heroes`)
      this.parents = res.data
    },
    async fetchCategories () {
      const res = await this.$http.get(`rest/categories`)
      this.categories = res.data
    },
    async fetchItems () {
      const res = await this.$http.get(`rest/items`)
      this.items = res.data
    },
    handleSuccess (res) {
      console.log(res)
      // 用下面的方法赋值会变慢
      this.$set(this.model, 'avatar', res.url)
      // this.model.icon = res.url
    }
  }
}
</script>

<style>
.avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
}
.avatar-uploader .el-upload:hover {
  border-color: #409EFF;
}
.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 178px;
  height: 178px;
  line-height: 178px;
  text-align: center;
}
.avatar {
  width: 178px;
  height: 178px;
  display: block;
}
</style>
