<template>
  <div>
    <el-card class='login-card'>
      <div slot='header'>
        <span>账号登陆</span>
      </div>
      <div>
        <el-form label-width="80px" @submit.native.prevent='login'>
          <el-form-item label='用户名：'>
            <el-input v-model='model.username'></el-input>
          </el-form-item>
          <el-form-item label='密码：'>
            <el-input v-model='model.password'></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type='primary' native-type='submit'>登陆</el-button>
          </el-form-item>
        </el-form>
      </div>
    </el-card>
  </div>
</template>

<script>
export default {
  data () {
    return {
      model: {}
    }
  },
  methods: {
    async login () {
      const res = await this.$http.post('/login', { username: this.model.username, password: this.model.password })
      console.log(res)
      if (res.data.token) {
        localStorage.setItem('token', res.data.token)
        this.$router.push('/')
        this.$message({
          type: 'success',
          message: '登陆成功'
        })
      }
    }
  }
}
</script>

<style>
.login-card{
  width: 35rem;
  margin: 10rem auto;
}
</style>
