<template>
  <div>
    <h1>{{id? "编辑":"新建"}}广告</h1>
    <el-form label-width="80px" @submit.native.prevent="save">
      <el-form-item label="广告名称">
        <el-input v-model='model.name'></el-input>
      </el-form-item>
      <el-form-item label="其他信息">
        <el-button @click='model.items.push({})'><i class="el-icon-plus"></i>添加广告</el-button>
        <el-row type='flex' style='flex-flow:wrap;'>
          <el-col :md='24' v-for='(item, index) in model.items' :key='index'>
            <el-form-item label="广告地址">
              <el-input v-model="item.url"></el-input>
            </el-form-item>
            <el-form-item label="广告图片" style='margin-top:10px;'>
              <el-upload
                class="avatar-uploader"
                :action="$http.defaults.baseURL+'/upload'"
                :show-file-list="false"
                :on-success="res => $set(item, 'image', res.url)">
                <img v-if="item.image" :src="item.image" class="avatar">
                <i v-else class="el-icon-plus avatar-uploader-icon"></i>
              </el-upload>
            </el-form-item>
            <el-button type='danger' @click='model.items.splice(index, 1)'>删除</el-button>
          </el-col>
        </el-row>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" native-type="submit">立即创建</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  data () {
    return {
      model: { name: '', items: [] }
    }
  },
  created () {
    this.id && this.fetch()
  },
  props: {
    id: String
  },
  methods: {
    async save () {
      // eslint-disable-next-line no-unused-vars
      let res
      if (this.id) {
        res = await this.$http.put(`rest/ads/${this.id}`, this.model)
      } else {
        res = await this.$http.post('rest/ads', this.model)
      }
      this.$message({
        type: 'success',
        message: '保存成功'
      })
      this.$router.push('/ads/list')
    },
    async fetch () {
      const res = await this.$http.get(`rest/ads/${this.id}`)
      this.model = res.data
    }
  }
}
</script>

